.stickers-picker-header {
    min-height: 44px;
    border-bottom-width: 1px;
    border-bottom-style: solid;
    overflow: hidden;
}

.stickers-picker-header-scroll {
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    overflow-y: hidden;
    padding-bottom: 16px;
    margin-bottom: -16px;
    position: relative;
    background: transparent;
}

.stickers-picker-header-items {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.stickers-picker-header-sticker {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    min-width: 32px;
    padding: 6px;
}

.stickers-picker-header-anchor {
    height: 3px;
    width: 44px;
    will-change: transform;
    position: absolute;
    bottom: 16px;
    transition: -webkit-transform 500ms ease-out;
    transition: transform 500ms ease-out;
    transition: transform 500ms ease-out, -webkit-transform 500ms ease-out;
    /*display: none;*/
}

