.stickers-picker {
    width: 338px;
    height: 358px;
    background: transparent;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.stickers-picker-scroll {
    background: transparent;
    margin-right: -15px;
    padding: 0 12px 6px 6px;
    height: 356px;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
}

